<template>
  <div class="login-container pull-height"
       @keyup.enter.native="handleLogin">
    <div class="bgDiv">
        <div class="picDiv">
            <img class="loginPic" src="@/assets/images/bg2.png">
        </div>
         <div class="loginDiv">
              <div class="login_logo">
                    <img src="@/assets/images/logo-blue.png">
              </div>
              <div class="login_text">
                   <!-- <avue-form ref="form" v-model="obj" :option="option" @submit="submit"></avue-form> -->
                   <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="80px" class="demo-ruleForm">
                     <el-form-item label="用户名" prop="userName" class="loginStyle">
                       <el-input v-model="ruleForm.userName"></el-input>
                     </el-form-item>
                     <el-form-item label="密码" prop="password" class="loginStyle">
                       <el-input type="password" v-model="ruleForm.password"></el-input>
                     </el-form-item>
                     <el-form-item class="loginStyle">
                        <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
                      </el-form-item>
                   </el-form>
              </div>
        </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'login',
  data () {
    return {
      ruleForm: {
        // userName: 'admin',
        // password: 'tskj1234'
        userName: 'admin',
        password: '0'
      },
      rules: {
        userName: [
          { required: true, message: '请输入用户名', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ]
      },
      obj: {}
    }
  },
  methods: {
    submitForm (formName) {
      let that = this
      this.$refs[formName].validate(function (valid) {
        if (valid) {
          console.log('结果', that.ruleForm)
          that.$axios.post(that.api + '/Login' + that.actionExt, {
            userName: that.ruleForm.userName,
            password: that.ruleForm.password
          }).then(function (response) {
            console.log(response)
            if (response.data.code === 0) {
              that.$router.push({
                name: 'archivelist'
              })
            } else {
              that.$message.error(response.data.errMsg)
            }
          }).catch(function (error) {
            if (error.response) {
              var status = error.response.status.toString()
              console.log('转转类型之后', typeof (status))
              if (status === '50') {
                that.$message.error('服务器在尝试处理请求时发生内部错误')
              } else if (status.substring(0, 1) === '4') {
                that.$message.error('请求出错，可能是服务器未找到页面')
              } else {
                that.$message.error('未知错误')
              }
            }
          })
        } else {
          return false
        }
      })
    }
  }
}
</script>

<style lang="scss">
.login-container {
  display: flex;
  align-items: center;
  justify-content: space-around;
  background: rgba(0, 0, 0, 0.2);
  position: relative;
}
.login-container::before {
  z-index: -999;
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-image: url("/static/assets/login.png");
  background-size: cover;
}
.pull-height {
  height: 100%;
}
.bgDiv {
  position: absolute;
  width: 950px;
  height: 520px;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
}

.picDiv {
  position: absolute;
  width: 550px;
  height: 550px;
  top: 50%;
  left: 0;
  margin-top: -275px;
}
.loginPic {
  position: relative;
  z-index: 1;
}

.loginDiv {
  position: absolute;
  width: 400px;
  height: 492px;
  background-color: #f4f4f4;
  top: 50%;
  right: -24px;
  margin-top: -220px;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  box-shadow: 1px 1px 30px #d2d2d2, 1px 1px 30px #d2d2d2;
}

.login_logo {
  width: 100%;
  text-align: center;
  padding-top: 60px;
}

.login_text {
  width: 100%;
  height: 250px;
  text-align: center;
  padding-top: 20px;
}

.loginStyle .el-form-item__content{
  width: 68%!important;
}
.loginStyle .el-form-item__label{
  text-align: right!important;
}

</style>



